
*{
  margin: 0;
  padding: 0;
}

#index div{
  width: 100%;
  text-align: center;
}
#index span{
  display: inline-block;
  width: 25%;
  text-align: right;
}
#index input{
  height: 2rem;
  outline: none;
  border: 0.5px solid #0000ff;
  border-radius: 5px;
  padding-left: 5px;
  font-size: 1rem;
}
#index .tip{
  margin-top: 6.25rem;
}
#index .account{
  margin-top: 5rem;
}
#index .password{
  margin-top: 1.25rem;
}
#index .next{
  margin: 100px auto 0;
  border: 0.5px solid blue;
  width: 45%;
  padding: 10px 0;
  border-radius: 8px;
}
#index .nexted{
  margin: 100px auto 0;
  border: 0.5px solid blue;
  width: 45%;
  padding: 10px 0;
  border-radius: 8px;
  color: #fff;
  background-image: -webkit-gradient(linear,left top, left bottom,from(#7868FF),to(blue));
  background-image: linear-gradient(to bottom,#7868FF,blue);
  -webkit-animation: next 500ms;
          animation: next 500ms;
}
@-webkit-keyframes next{
from{opacity: 0;
}
to{opacity: 1;
}
}
#index .err{
  margin-top: 80px;
  border: 1px solid red;
  width: 40%;
  padding: 10px 0;
  border-radius: 5px;
  margin-left: 30%;
  color: red;
}
#index .errs{
  margin-top: 80px;
  border: 1px solid red;
  width: 40%;
  padding: 10px 0;
  border-radius: 5px;
  margin-left: 30%;
  color: red;
}
#index .right{
  margin-top: 80px;
  border: 1px solid #4196FF;
  width: 40%;
  padding: 10px 0;
  border-radius: 5px;
  margin-left: 30%;
  -webkit-animation: right 3000ms infinite linear;
          animation: right 3000ms infinite linear;
}
@-webkit-keyframes right{
0%{background-color: transparent;color: #4196FF;
}
50%{background-color: #4196FF;color: #ffffff;
}
100%{background-color: transparent;color: #4196FF;
}
}
#index a{
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  height: 50px;
  width: 100%;
}

