	border-radius: 100%;
}
.playPage .buttons div:first-child img{
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	width: 18px;
	height: 18px;
}
.playPage .buttons div:last-child img{
	width: 18px;
