}
.setPage{
	position: fixed;
	bottom: 0;
	z-index: 3;
	width: 100vw;
	height: 100vh;
}
.setPage .setPageTop{
	height: 60%;
}
.setPage .setPageDow{
	height: 40%;
	background-color: #F1F1F1;
	border-radius: 8px 8px 0 0;
	-webkit-animation: setPageDowShow 300ms;
	        animation: setPageDowShow 300ms;
}
@-webkit-keyframes setPageDowShow{
from{-webkit-transform: translateY(60vh);transform: translateY(60vh);
}
to{-webkit-transform: translateY(0);transform: translateY(0);
}
}
@keyframes setPageDowShow{
from{-webkit-transform: translateY(60vh);transform: translateY(60vh);
}
to{-webkit-transform: translateY(0);transform: translateY(0);
}
}
